<template>
     <v-layout row>
    <v-flex xs12 sm6 offset-sm3>
      <v-card :raised="false" style="box-shadow: none; border-radius: 20px; padding: 40px">

        <v-layout row wrap>
        
          <v-flex xs12 v-for="item in items" :key="item.name">
            <v-card style="box-shadow: none">
              <v-layout>
                <v-flex xs3>
                  <v-img
                    :src="item.photo_url"
                    height="125px"
                    contain
                  ></v-img>
                </v-flex>
                <v-flex xs9>
                  <v-card-title primary-title>
                    <div>
                      <div class="headline">{{ item.name }}</div>
                      <div>{{ item.description }}</div>
                      <div class="headline"><b>{{ item.price }}</b></div>

                    </div>
                  </v-card-title>
                </v-flex>
                
              </v-layout>
            </v-card>
          </v-flex>
          

        
        </v-layout>
   
         <v-btn
              absolute
              dark
              fab
              top
              right
              color="pink"
              style="margin-top: 20px"
              @click="addMenuItem"
            >
              <v-icon>add</v-icon>
            </v-btn>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script> 
 import { find } from 'lodash'
 import firebase from '../service/firebase'
export default {
    data () {
      return {
        store_items: null,
        items:null
      }
    },
    mounted(){
      let items = firebase.database.ref('menus').child(this.$route.params.menu_id).child('categories').child(this.$route.params.cat_id+"/")
      // console.log(obj);
      
      let vm = this
        items.on("value", function(snapshot, prevChildKey) {
                // vm.menu = newPost
                var curr_items = snapshot.val();
                vm.items = curr_items
                // vm.store_items = curr_items
                console.log(curr_items);
                
              });
    },
    methods:{
      addMenuItem(){
        let random = Math.floor(Math.random() * 100 + 1);
        let price = random * 10;
         firebase.database.ref('menus').child(this.$route.params.menu_id).child('categories').child(this.$route.params.cat_id).push(
         {
              'name': 'test '+random,
              'description': "A three-egg omelette made with sauted mushroom, baby spinach & gouda cheese",
              'price':"Ksh "+price,
              'thumb_url': "https://images.unsplash.com/photo-1533622597524-a1215e26c0a2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=200&q=60",
              'photo_url': "https://images.unsplash.com/photo-1533622597524-a1215e26c0a2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60",
              'quantity': 1,
              'created_at': -1 * new Date().getTime(),
            }
       )
      }
    }
  }
</script>

<style>

</style>



